<template>
  <div class="chat-ai-layout">
    <aside class="sidebar">
      <div class="sidebar-header">
        <h2>AIËÅäÂ§©</h2>
        <p class="subtitle">Êô∫ËÉΩÂØπËØùÂä©Êâã</p>
      </div>
      <div class="chat-list">
        <div
          v-for="chat in chatList"
          :key="chat.chatId"
          :class="['chat-item', chat.chatId === currentChatId ? 'active' : '']"
          @click="switchChat(chat.chatId)"
        >
          <div class="chat-summary">
            {{ chat.summary || 'ÔºàÁ©∫‰ºöËØùÔºâ' }}
          </div>
          <div class="chat-time">{{ formatTime(chat.createTime) }}</div>
        </div>
      </div>
      <button class="new-chat-btn" @click="newChat">+ Êñ∞Âª∫‰ºöËØù</button>
    </aside>
    <main class="chat-main">
      <div class="chat-header" :class="{ 'collapsed': isHeaderCollapsed }">
        <div class="header-content">
          <h1>AIËÅäÂ§©Âä©Êâã</h1>
          <div class="features">
            <span class="feature-tag">Êô∫ËÉΩÂØπËØù</span>
            <span class="feature-tag">Áü•ËØÜÈóÆÁ≠î</span>
            <span class="feature-tag">Ê∑±Â∫¶ÊÄùËÄÉ</span>
            <span class="feature-tag">ÂàõÊÑèÂÜô‰Ωú</span>
          </div>
        </div>
        <button class="collapse-btn" @click="toggleHeader" :title="isHeaderCollapsed ? 'Â±ïÂºÄÊ†áÈ¢ò' : 'Êî∂Ëµ∑Ê†áÈ¢ò'">
          {{ isHeaderCollapsed ? '‚ñº' : '‚ñ≤' }}
        </button>
      </div>
      <div class="chat-window">
        <div class="messages">
          <div v-for="(msg, idx) in currentMessages" :key="idx" :class="['message', msg.role]">
            <div class="message-avatar">
              <div v-if="msg.role === 'user'" class="user-avatar">üë§</div>
              <div v-else class="ai-avatar">ü§ñ</div>
            </div>
            <div class="message-content">
              <!-- Áî®Êà∑Ê∂àÊÅØ -->
              <template v-if="msg.role === 'user'">
                <div class="user-message">
                  <span class="text">{{ msg.text }}</span>
                </div>
              </template>
              
              <!-- AIÊ∂àÊÅØ -->
              <template v-else>
                <div class="ai-message-container">
                  <!-- ÊÄùËÄÉËøáÁ®ãÈÉ®ÂàÜ -->
                  <div v-if="msg.think" class="ai-think">
                    <div class="ai-think-header" @click="toggleThink(idx)">
                      <span class="think-toggle">{{ thinkStates[idx] ? '‚ñº' : '‚ñ∂' }}</span>
                      {{ (isThinking && idx === currentMessages.length - 1 && loading) ? 'ÊÄùËÄÉ‰∏≠' : 'ÊÄùËÄÉËøáÁ®ã' }}
                      <span v-if="isThinking && idx === currentMessages.length - 1 && loading" class="thinking-dot"></span>
                    </div>
                    <!-- ÈªòËÆ§Â±ïÂºÄÊÄùËÄÉÂÜÖÂÆπ -->
                    <div v-show="thinkStates[idx] !== false" class="ai-think-content">{{ msg.think }}</div>
                  </div>
                  
                  <!-- Âä†ËΩΩÂä®ÁîªÔºåÂΩìËøôÊòØÊúÄÂêé‰∏ÄÊù°Ê∂àÊÅØ‰∏îÊ≠£Âú®Âä†ËΩΩÊó∂ÊòæÁ§∫ -->
                  <div v-if="loading && idx === currentMessages.length - 1 && !msg.text && !msg.think" class="loading-dots">
                    <span class="dot"></span>
                    <span class="dot"></span>
                    <span class="dot"></span>
                  </div>
                  
                  <!-- ÊúÄÁªàÂõûÂ§çÈÉ®ÂàÜ -->
                  <div v-if="msg.text" class="ai-response">
                    <div class="response-text" v-html="formatResponse(msg.text)"></div>
                  </div>
                </div>
              </template>
            </div>
          </div>
          
        </div>
        <div class="input-bar">
          <div class="input-wrapper">
          <textarea
            v-model="input"
            @input="autoResizeInput"
            @keydown="handleKeydown"
            placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÈóÆÈ¢òÊàñÊÉ≥Ê≥ï..."
            :disabled="loading"
            class="input-auto textarea-auto"
            rows="1"
            style="resize:none;overflow:hidden;"
          />
            <div class="input-actions">
              <button class="send-btn" @click="send" :disabled="loading || !input.trim()">
                <span v-if="loading">ÂèëÈÄÅ‰∏≠...</span>
                <span v-else>ÂèëÈÄÅ</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { ref, watch, onMounted } from 'vue'
import axios from 'axios'

function genId() {
  return 'chat_' + Date.now() + '_' + Math.random().toString(36).slice(2, 8)
}

export default {
  name: 'ChatAI',
  setup() {
    const chatList = ref([])
    const currentChatId = ref('')
    const input = ref('')
    const loading = ref(false)
    const streamingText = ref('')
    const thinkStates = ref({})
    const isThinking = ref(false)
    const thinkingContent = ref('')
    const showThinkingContent = ref(true) // ÊÄùËÄÉÂÜÖÂÆπÈªòËÆ§Â±ïÂºÄ
    const isThinkingComplete = ref(false)
    const currentMessages = ref([])
    const isHeaderCollapsed = ref(false)
    
    // ‰∏∫ÊØè‰∏™‰ºöËØùÁª¥Êä§Áã¨Á´ãÁöÑÊ∂àÊÅØÂíåÁä∂ÊÄÅ
    const sessionMessages = ref({}) // { chatId: messages[] }
    const sessionLoading = ref({}) // { chatId: boolean }
    const sessionStreaming = ref({}) // { chatId: string }
    const sessionThinkStates = ref({}) // { chatId: { msgIndex: boolean } }

    // Ëé∑Âèñ‰ºöËØùÂàóË°®
    const fetchChatList = async () => {
      try {
        const resp = await axios.get('/ai/history/chat/with-time')
        // {chatId: createTime} Êò†Â∞Ñ
        const chatIdsWithTime = resp.data
        const chatListWithTitles = []
        
        // ‰∏∫ÊØè‰∏™‰ºöËØùËé∑ÂèñÁ¨¨‰∏ÄÊù°Ê∂àÊÅØ‰Ωú‰∏∫Ê†áÈ¢ò
        for (const [chatId, createTime] of Object.entries(chatIdsWithTime)) {
          try {
            const messagesResp = await axios.get(`/ai/history/chat/${chatId}`)
            const messages = messagesResp.data
            let title = 'ÔºàÁ©∫‰ºöËØùÔºâ'
            
            // ÊâæÂà∞Á¨¨‰∏ÄÊù°Áî®Êà∑Ê∂àÊÅØ‰Ωú‰∏∫Ê†áÈ¢ò
            const firstUserMessage = messages.find(msg => msg.role === 'user')
            if (firstUserMessage && firstUserMessage.content) {
              title = firstUserMessage.content.slice(0, 20) + (firstUserMessage.content.length > 20 ? '...' : '')
            }
            
            chatListWithTitles.push({ 
              chatId, 
              summary: title,
              createTime: createTime
            })
          } catch (error) {
            // Â¶ÇÊûúËé∑ÂèñÊ∂àÊÅØÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§Ê†áÈ¢ò
            chatListWithTitles.push({ 
              chatId, 
              summary: 'ÔºàÁ©∫‰ºöËØùÔºâ',
              createTime: createTime
            })
          }
        }
        
        // ÊåâÂàõÂª∫Êó∂Èó¥ÊéíÂ∫èÔºåÊúÄÊñ∞ÁöÑÂú®ÂâçÈù¢
        chatListWithTitles.sort((a, b) => b.createTime - a.createTime)
        
        chatList.value = chatListWithTitles
        // Ëá™Âä®ÈÄâ‰∏≠Á¨¨‰∏Ä‰∏™
        if (chatList.value.length > 0 && !currentChatId.value) {
          currentChatId.value = chatList.value[0].chatId
        }
      } catch (error) {
        console.error('Ëé∑Âèñ‰ºöËØùÂàóË°®Â§±Ë¥•:', error)
      }
    }

    // Ëé∑ÂèñÂΩìÂâç‰ºöËØùÊ∂àÊÅØ
    const fetchMessages = async (chatId) => {
      if (!chatId) return
      const resp = await axios.get(`/ai/history/chat/${chatId}`)
      // resp.data: [{role, content}]
      // ÂÖºÂÆπÂéüÊúâÁªìÊûÑ
      const messages = resp.data.map(msg => {
        // ÂÖºÂÆπAIÊÄùËÄÉËøáÁ®ãÔºàÂ¶ÇÊûúcontentÈáåÊúâ<think>Ê†áÁ≠æÔºâ
        if (msg.role === 'assistant' && msg.content && msg.content.includes('<think>')) {
          const thinkMatch = msg.content.match(/<think>([\s\S]*?)<\/think>/)
          return {
            role: 'ai',
            text: msg.content.replace(/<think>[\s\S]*?<\/think>/, '').trim(),
            think: thinkMatch ? thinkMatch[1].trim() : ''
          }
        } else if (msg.role === 'assistant') {
          return { role: 'ai', text: msg.content, think: '' }
        } else {
          return { role: 'user', text: msg.content }
        }
      })
      
      // Êõ¥Êñ∞‰ºöËØùÊ∂àÊÅØ
      sessionMessages.value[chatId] = messages
      // Â¶ÇÊûúÊòØÂΩìÂâç‰ºöËØùÔºåÊõ¥Êñ∞currentMessages
      if (chatId === currentChatId.value) {
        currentMessages.value = messages
                  // ‰∏∫ÊúâÊÄùËÄÉËøáÁ®ãÁöÑÊ∂àÊÅØËÆæÁΩÆÈªòËÆ§Â±ïÂºÄÁä∂ÊÄÅ
        const thinkStatesForChat = {}
        messages.forEach((msg, idx) => {
          if (msg.think) {
            thinkStatesForChat[idx] = true // ÈªòËÆ§Â±ïÂºÄ
          }
        })
        sessionThinkStates.value[chatId] = thinkStatesForChat
        thinkStates.value = thinkStatesForChat
        // ÊªöÂä®Âà∞Â∫ïÈÉ®
        scrollToBottom()
      }
    }

    // Êñ∞Âª∫‰ºöËØù
    const newChat = async () => {
      const id = genId()
      currentChatId.value = id
      sessionMessages.value[id] = []
      currentMessages.value = []
      // ‰∏çÁ´ãÂç≥Ê∑ªÂä†Âà∞ËÅäÂ§©ÂàóË°®ÔºåÁ≠âÂèëÈÄÅÊ∂àÊÅØÊó∂ÂÜçÊ∑ªÂä†
    }

    // ÂàáÊç¢‰ºöËØù
    const switchChat = async (id) => {
      currentChatId.value = id
      // Â¶ÇÊûú‰ºöËØùÊ∂àÊÅØÂ∑≤Â≠òÂú®ÔºåÁõ¥Êé•‰ΩøÁî®
      if (sessionMessages.value[id]) {
        currentMessages.value = sessionMessages.value[id]
        thinkStates.value = sessionThinkStates.value[id] || {}
        // Ê£ÄÊü•ÂΩìÂâç‰ºöËØùÊòØÂê¶Ê≠£Âú®ÊÄùËÄÉ‰∏≠ - Âè™ÊúâÂú®ÁúüÊ≠£ÊÄùËÄÉ‰∏≠‰∏îÊú™ÂÆåÊàêÊó∂ÊâçÊòæÁ§∫
        const isCurrentlyThinking = sessionLoading.value[id] && 
          sessionStreaming.value[id] && 
          sessionStreaming.value[id].includes('<think>') && 
          !sessionStreaming.value[id].includes('</think>')
        isThinking.value = isCurrentlyThinking
        streamingText.value = sessionStreaming.value[id] || ''
        // ÊªöÂä®Âà∞Â∫ïÈÉ®
        scrollToBottom()
      } else {
        // Âê¶Âàô‰ªéÂêéÁ´ØËé∑Âèñ
        await fetchMessages(id)
        // Êñ∞Ëé∑ÂèñÁöÑ‰ºöËØù‰∏ç‰ºöÊúâÊÄùËÄÉÁä∂ÊÄÅ
        isThinking.value = false
        streamingText.value = ''
      }
    }

    // ÊªöÂä®Âà∞Â∫ïÈÉ®ÂáΩÊï∞
    const scrollToBottom = () => {
      setTimeout(() => {
        const messagesEl = document.querySelector('.messages')
        if (messagesEl) {
          messagesEl.scrollTop = messagesEl.scrollHeight
        }
      }, 10)
    }
    
    // ÂàáÊç¢Â§¥ÈÉ®Êî∂Áº©Áä∂ÊÄÅ
    const toggleHeader = () => {
      isHeaderCollapsed.value = !isHeaderCollapsed.value
    }
    
    // Ê†ºÂºèÂåñÊó∂Èó¥
    const formatTime = (timestamp) => {
      if (timestamp) {
        const date = new Date(timestamp)
        return date.toLocaleDateString() + ' ' + date.toLocaleTimeString()
      }
      return ''
    }
    
    // Ê†ºÂºèÂåñÂìçÂ∫îÊñáÊú¨
    const formatResponse = (text) => {
      if (!text) return ''
      // ÁÆÄÂçïÁöÑÊç¢Ë°åÁ¨¶Â§ÑÁêÜ
      return text.replace(/\n/g, '<br>')
    }

    // ÂèëÈÄÅÊ∂àÊÅØÔºàÊîØÊåÅÂêéÂè∞Â§ÑÁêÜÔºâ
    const send = async () => {
      if (!input.value.trim() || loading.value) return
      
      const chatId = currentChatId.value
      // Á°Æ‰øù chatId Â≠òÂú®ÔºåÂ¶ÇÊûú‰∏çÂ≠òÂú®ÂàôÁîüÊàê‰∏Ä‰∏™Êñ∞ÁöÑ
      if (!chatId) {
        const newId = genId()
        currentChatId.value = newId
        sessionMessages.value[newId] = []
      }
      
      const prompt = input.value
      input.value = ''
      // ËÆæÁΩÆÂΩìÂâç‰ºöËØù‰∏∫Âä†ËΩΩÁä∂ÊÄÅ
      sessionLoading.value[currentChatId.value] = true
      loading.value = true
      
      // Ëé∑ÂèñÂΩìÂâç‰ºöËØùÁöÑÊ∂àÊÅØÂàóË°®
      let messages = sessionMessages.value[currentChatId.value] || []
      
      // Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÊù°Ê∂àÊÅØÔºåÂÖàÊ∑ªÂä†Âà∞ËÅäÂ§©ÂàóË°®
      if (messages.length === 0) {
        const currentTime = Date.now()
        chatList.value.unshift({ 
          chatId: currentChatId.value, 
          summary: prompt.slice(0, 20) + (prompt.length > 20 ? '...' : ''),
          createTime: currentTime
        })
      } else {
        // Â¶ÇÊûú‰∏çÊòØÁ¨¨‰∏ÄÊù°Ê∂àÊÅØÔºåÊõ¥Êñ∞‰ºöËØùÊó∂Èó¥
        const currentChat = chatList.value.find(c => c.chatId === currentChatId.value)
        if (currentChat) {
          currentChat.createTime = Date.now()
        }
      }
      
      // ÂÖàÊ∑ªÂä†Áî®Êà∑Ê∂àÊÅØÂà∞ÁïåÈù¢
      messages.push({ role: 'user', text: prompt })
      sessionMessages.value[currentChatId.value] = messages
      
      // Á´ãÂç≥ÂàõÂª∫‰∏Ä‰∏™Á©∫ÁöÑAIÊ∂àÊÅØÔºåÊòæÁ§∫"AIÔºö"ÔºåÈÅøÂÖçÂç°È°øÊÑü
      messages.push({ role: 'ai', text: '', think: '' })
      sessionMessages.value[currentChatId.value] = messages
      
      // Â¶ÇÊûúÊòØÂΩìÂâç‰ºöËØùÔºåÊõ¥Êñ∞currentMessages
      if (currentChatId.value === currentChatId.value) {
        currentMessages.value = messages
        // ÊªöÂä®Âà∞Â∫ïÈÉ®
        scrollToBottom()
      }
      
      // ÂêéÂè∞Â§ÑÁêÜAIÂõûÂ§ç
      processAIResponse(currentChatId.value, prompt, messages)
    }

    // ÂêéÂè∞Â§ÑÁêÜAIÂõûÂ§ç
    const processAIResponse = async (chatId, prompt, messages) => {
      try {
        // Áî® fetch Â§ÑÁêÜÊµÅ
        const url = axios.getUri({ url: '/ai/chat', params: { prompt, chatId } })
        const resp = await fetch(url, { method: 'GET' })
        if (!resp.body) throw new Error('Êó†ÊµÅÂºèÂìçÂ∫î')
        const reader = resp.body.getReader()
        let aiText = ''
        let inThinkingMode = false
        let thinkingCompleted = false
        
        // ÂàùÂßãÂåñ‰ºöËØùÁä∂ÊÄÅ
        sessionStreaming.value[chatId] = ''
        sessionThinkStates.value[chatId] = sessionThinkStates.value[chatId] || {}
        
        // ‰ΩøÁî®Â∑≤ÁªèÂàõÂª∫ÁöÑAIÊ∂àÊÅØÔºàÊúÄÂêé‰∏Ä‰∏™Ê∂àÊÅØÔºâ
        const tempMsgIdx = messages.length -1
        // ÈªòËÆ§Â±ïÂºÄÊÄùËÄÉËøáÁ®ã
        sessionThinkStates.value[chatId][tempMsgIdx] = true // Á°Æ‰øùÊÄùËÄÉËøáÁ®ãÈªòËÆ§Â±ïÂºÄ
        
        // Â¶ÇÊûúÊòØÂΩìÂâç‰ºöËØùÔºåÊõ¥Êñ∞currentMessages
        if (chatId === currentChatId.value) {
          currentMessages.value = messages
          // ÊªöÂä®Âà∞Â∫ïÈÉ®
          scrollToBottom()
        }
        
        // eslint-disable-next-line no-constant-condition
        while (true) {
          const { done, value } = await reader.read()
          if (done) break
          const chunk = new TextDecoder('utf-8').decode(value)
          aiText += chunk
          sessionStreaming.value[chatId] = aiText
          
          // Ê£ÄÊü•ÊòØÂê¶ËøõÂÖ•ÊÄùËÄÉÊ®°Âºè
          if (aiText.includes('<think>') && !inThinkingMode && !thinkingCompleted) {
            inThinkingMode = true
            const thinkStartIndex = aiText.indexOf('<think>') + '<think>'.length
            const thinkingContent = aiText.substring(thinkStartIndex).trim()
            messages[tempMsgIdx].think = thinkingContent
            // ÊµÅÂºèÊó∂Ê≠£ÂºèÂõûÁ≠îÂå∫Âüü‰∏çÊòæÁ§∫ÊÄùËÄÉÂÜÖÂÆπ
            messages[tempMsgIdx].text = ''
          }
          // Ê£ÄÊü•ÊòØÂê¶ÁªìÊùüÊÄùËÄÉÊ®°Âºè
          else if (inThinkingMode && aiText.includes('</think>')) {
            inThinkingMode = false
            thinkingCompleted = true
            const thinkMatch = aiText.match(/<think>([\s\S]*?)<\/think>/)
            if (thinkMatch) {
              messages[tempMsgIdx].think = thinkMatch[1].trim()
            }
            // Âè™ÊòæÁ§∫<think>...</think>‰πãÂêéÁöÑÂÜÖÂÆπ
            let cleanedText = aiText.split(/<think>[\s\S]*?<\/think>/)[1] || ''
            cleanedText = cleanedText.replace(/<think>|<\/think>/g, '').trim()
            cleanedText = cleanedText.replace(/\n\s*\n/g, '\n').trim()
            messages[tempMsgIdx].text = cleanedText
          } 
          // Âú®ÊÄùËÄÉÊ®°Âºè‰∏≠Êõ¥Êñ∞ÂÜÖÂÆπ
          else if (inThinkingMode && !thinkingCompleted) {
            const thinkStartIndex = aiText.indexOf('<think>') + '<think>'.length
            const thinkingContent = aiText.substring(thinkStartIndex).trim()
            messages[tempMsgIdx].think = thinkingContent
            // ÊµÅÂºèÊó∂Ê≠£ÂºèÂõûÁ≠îÂå∫Âüü‰∏çÊòæÁ§∫ÊÄùËÄÉÂÜÖÂÆπ
            messages[tempMsgIdx].text = ''
          } 
          // ÊÄùËÄÉÂÆåÊàêÂêéÊàñÊ≤°ÊúâÊÄùËÄÉËøáÁ®ãÔºåÁõ¥Êé•Êõ¥Êñ∞ÊñáÊú¨
          else {
            // Âè™ÊòæÁ§∫<think>...</think>‰πãÂêéÁöÑÂÜÖÂÆπÔºàÂ¶ÇÊûúÊúâÔºâÔºåÂê¶ÂàôÂÖ®ÈÉ®ÂÜÖÂÆπ
            if (aiText.includes('<think>') && aiText.includes('</think>')) {
              let cleanedText = aiText.split(/<think>[\s\S]*?<\/think>/)[1] || ''
              cleanedText = cleanedText.replace(/<think>|<\/think>/g, '').trim()
              cleanedText = cleanedText.replace(/\n\s*\n/g, '\n').trim()
              messages[tempMsgIdx].text = cleanedText
          } else {
              let cleanedText = aiText.replace(/<think>[\s\S]*?<\/think>/g, '').trim()
              cleanedText = cleanedText.replace(/<think>|<\/think>/g, '').trim()
              cleanedText = cleanedText.replace(/\n\s*\n/g, '\n').trim()
              messages[tempMsgIdx].text = cleanedText
            }
          }
          
          // Êõ¥Êñ∞‰ºöËØùÊ∂àÊÅØ
          sessionMessages.value[chatId] = [...messages]
          
          // Â¶ÇÊûúÊòØÂΩìÂâç‰ºöËØùÔºåÊõ¥Êñ∞currentMessagesÂπ∂ÊªöÂä®
          if (chatId === currentChatId.value) {
            currentMessages.value = [...messages]
            // Êõ¥Êñ∞ÂÖ®Â±ÄÊÄùËÄÉÁä∂ÊÄÅÁî®‰∫éUIÊòæÁ§∫ - Âè™ÊúâÂú®ÁúüÊ≠£ÊÄùËÄÉ‰∏≠Êó∂ÊâçÊòæÁ§∫
            isThinking.value = inThinkingMode && !thinkingCompleted
            streamingText.value = aiText
            // ÊªöÂä®Âà∞Â∫ïÈÉ®
            scrollToBottom()
            }
        }
        
        // Ëß£ÊûêAIÂõûÂ§ç‰∏≠ÁöÑÊÄùËÄÉËøáÁ®ã
        let thinkContent = ''
        let finalText = aiText
        
        // ÂΩªÂ∫ïÊ∏ÖÁêÜÊâÄÊúâ<think>Ê†áÁ≠æÂèäÂÖ∂ÂÜÖÂÆπÔºåÂπ∂Á°Æ‰øùmsg.textÂè™ÊòæÁ§∫Ê≠£ÂºèÂõûÂ§ç
        const thinkMatch = aiText.match(/<think>([\s\S]*?)<\/think>/)
        if (thinkMatch) {
          thinkContent = thinkMatch[1].trim()
          // Âè™Âèñ<think>...</think>‰πãÂêéÁöÑÂÜÖÂÆπ‰Ωú‰∏∫Ê≠£ÂºèÂõûÂ§ç
          const afterThink = aiText.split(/<think>[\s\S]*?<\/think>/)[1]
          finalText = afterThink ? afterThink.trim() : ''
        } else {
          // Ê≤°ÊúâÊÄùËÄÉÊ†áÁ≠æÔºåÂÖ®ÈÉ®ÂÜÖÂÆπ‰∏∫Ê≠£ÂºèÂõûÂ§ç
          finalText = aiText.trim()
        }
        // È¢ùÂ§ñÊ∏ÖÁêÜÊÆãÁïôÊ†áÁ≠æ
        finalText = finalText.replace(/<think>|<\/think>/g, '').trim()
        // Ê∏ÖÁêÜÂ§ö‰ΩôÁ©∫Ë°å
        finalText = finalText.replace(/\n\s*\n/g, '\n').trim()
        
        messages[tempMsgIdx] = {
          role: 'ai',
          text: finalText,
          think: thinkContent
        }
        
        sessionMessages.value[chatId] = [...messages]
        if (thinkContent) {
          // ÈªòËÆ§Â±ïÂºÄÊÄùËÄÉËøáÁ®ã
          sessionThinkStates.value[chatId][tempMsgIdx] = true // Á°Æ‰øùÊÄùËÄÉËøáÁ®ãÈªòËÆ§Â±ïÂºÄ
        }
        
        // Â¶ÇÊûúÊòØÂΩìÂâç‰ºöËØùÔºåÊõ¥Êñ∞currentMessages
        if (chatId === currentChatId.value) {
          currentMessages.value = [...messages]
          // ÊÄùËÄÉÂÆåÊàêÂêéÔºåÁ°Æ‰øù‰∏çÊòæÁ§∫ÊÄùËÄÉ‰∏≠Áä∂ÊÄÅ
          isThinking.value = false
          streamingText.value = ''
          // ÊªöÂä®Âà∞Â∫ïÈÉ®
          scrollToBottom()
        }
        
        // ÂèëÈÄÅÂêéÂà∑Êñ∞‰ºöËØùÂàóË°®‰ª•Ëé∑ÂèñÊúÄÊñ∞ÁöÑÊó∂Èó¥
        await fetchChatList()
      } catch (e) {
        const errorMsgIdx = messages.length -1
        messages[errorMsgIdx] = { role: 'ai', text: '[AIÊµÅÂºèÂºÇÂ∏∏] ' + e.message, think: '' }
        sessionMessages.value[chatId] = [...messages]
        if (chatId === currentChatId.value) {
          currentMessages.value = [...messages]
          // ÊªöÂä®Âà∞Â∫ïÈÉ®
          scrollToBottom()
        }
      } finally {
        // Ê∏ÖÈô§‰ºöËØùÁä∂ÊÄÅ
        sessionLoading.value[chatId] = false
        sessionStreaming.value[chatId] = ''
        // Â¶ÇÊûúÊòØÂΩìÂâç‰ºöËØùÔºåÊ∏ÖÈô§ÂÖ®Â±ÄÁä∂ÊÄÅ
        if (chatId === currentChatId.value) {
        loading.value = false
        streamingText.value = ''
        isThinking.value = false
        isThinkingComplete.value = false
        thinkingContent.value = ''
          showThinkingContent.value = true
        }
      }
    }
    
    const toggleThink = (idx) => {
      const chatId = currentChatId.value
      if (!sessionThinkStates.value[chatId]) {
        sessionThinkStates.value[chatId] = {}
      }
      // Â¶ÇÊûúÊú™ËÆæÁΩÆÔºåÂàôÈªòËÆ§‰∏∫trueÔºàÂ±ïÂºÄÁä∂ÊÄÅÔºâ
      if (sessionThinkStates.value[chatId][idx] === undefined) {
        sessionThinkStates.value[chatId][idx] = true
      }
      sessionThinkStates.value[chatId][idx] = !sessionThinkStates.value[chatId][idx]
      thinkStates.value = sessionThinkStates.value[chatId]
    }
    
    const toggleThinkingState = () => {
      showThinkingContent.value = !showThinkingContent.value
    }

    // Â§ÑÁêÜÈîÆÁõò‰∫ã‰ª∂
    const handleKeydown = (e) => {
      if (e.key === 'Enter') {
        if (e.shiftKey) {
          // Shift+Enter: Êç¢Ë°å
          return;
        } else {
          // Enter: ÂèëÈÄÅÊ∂àÊÅØ
          e.preventDefault();
          send();
        }
      }
    };

    // Ëá™Âä®Ë∞ÉÊï¥ËæìÂÖ•Ê°ÜÈ´òÂ∫¶
    const autoResizeInput = (e) => {
      const textarea = e.target;
      textarea.style.height = 'auto';
      textarea.style.height = textarea.scrollHeight + 'px';
    };

    // ÂàùÂßãÂåñÂä†ËΩΩ
    onMounted(async () => {
      await fetchChatList()
      if (currentChatId.value) {
        await fetchMessages(currentChatId.value)
      }
      // È°µÈù¢Âä†ËΩΩÂÆåÊàêÂêéÊªöÂä®Âà∞Â∫ïÈÉ®
      scrollToBottom()
    })

    // ÁõëÂê¨‰ºöËØùÂàáÊç¢Ëá™Âä®ÊãâÂèñÊ∂àÊÅØ
    watch(currentChatId, async (id) => {
      if (id) {
        if (sessionMessages.value[id]) {
          currentMessages.value = sessionMessages.value[id]
          thinkStates.value = sessionThinkStates.value[id] || {}
          // ÊªöÂä®Âà∞Â∫ïÈÉ®
          scrollToBottom()
        } else {
          await fetchMessages(id)
        }
      }
    })

    return {
      chatList,
      currentChatId,
      currentMessages,
      input,
      send,
      newChat,
      switchChat,
      loading,
      streamingText,
      thinkStates,
      toggleThink,
      isThinking,
      thinkingContent,
      showThinkingContent,
      toggleThinkingState,
      autoResizeInput,
      formatTime,
      formatResponse,
      isHeaderCollapsed,
      toggleHeader,
      handleKeydown
    }
  }
}
</script>

